.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 25px;
  background-color: #ffe0f0;
  border-bottom: 2px solid #ffcce0;
  font-family: 'Comic Sans MS', cursive, sans-serif;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.logo {
  font-size: 1.4rem;
  font-weight: bold;
  color: #d63384;
}

.nav-right a {
  margin-left: 20px;
  text-decoration: none;
  font-size: 1rem;
  color: #ff69b4;
  transition: all 0.3s ease;
}

.nav-right a:hover {
  color: #c71585;
  font-weight: bold;
}
